@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Paaji+2&family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;

  min-height: 50vh;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-body {
  background-color: #340d16;

  min-height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-body-NameBlocks {
  /* border: 3px solid white; */
  width: 100%;
  height: 250px;

  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-body-Text {
  font-family: 'Roboto Mono', monospace;
  /* border: 3px solid white; */
  width: 100%;
  height: 100%;
  align-self: center;
  justify-self: center;
}


.App-link {
  color: #61dafb;
}


.UpLeftDiv {
  background-color: #9198ff;
  font-family: 'Baloo Paaji 2', cursive;
  margin: 10px;
  width: 95%;
  height: 100px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.UpRightDiv {
  background-color: #9198ff;
  font-family: 'Baloo Paaji 2', cursive;
  margin: 10px;
  width: 95%;
  height: 100px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.DownLeftDiv {
  background-color: #9198ff;
  font-family: 'Baloo Paaji 2', cursive;
  margin: 10px;
  width: 95%;
  height: 100px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.DownRightDiv {
  background-color: #9198ff;
  font-family: 'Baloo Paaji 2', cursive;
  margin: 10px;
  width: 95%;
  height: 100px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

